.cont {
  padding: 0 20%;
  height: 100vh;
}

.manager {
  float: left;
  text-align: left;
  margin-bottom: 100px;
}

.manager h4 {
  font-size: 45px;
  color: #FF3948;
  line-height: 20px;
}

.manager h3 {
  font-size: 68px;
  color: #0D5C63;
  line-height: 20px;
}

.manager p {
  font-size: 20px;
  color: #0D5C63;
  margin: 10px;
}

.flexin {
  clear: both;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 75px;
  text-align: left;
}

.flexin h4 {
  font-size: 35px;
  color: #FF3948;
  line-height: 20px;
}

.flexin h3 {
  font-size: 45px;
  color: #0D5C63;
  line-height: 20px;
}

.flexin p {
  font-size: 20px;
  color: #0D5C63;
  margin: 0px;
}

.kont {
  width: 450px;
}