.shadow {
	filter: drop-shadow(6px 6px 5px rgba(0,0,0,0.21));
}
.whiteBackground {
	margin-top: -300px;
}

.containerBlue {
	display: flex;
	padding: 0 6%;
	justify-content: center;
	align-items: center;
}

.blueBox {
	width: 660px;
	height: 400px;
-webkit-clip-path: polygon(0 0, 100% 35%, 86% 95%, 8% 100%);
clip-path: polygon(0 0, 100% 35%, 86% 95%, 8% 100%);
	background: url('./asset1.jpg') center center no-repeat,
	rgba(102, 255, 193, 0.8);
	background-size: cover;
	margin-left: -100px;
	z-index: 10;
}

.blueBox:before {
    content: '';
  	position: absolute;
  	top: 0;
  	right: 0;
  	bottom: 0;
  	left: -200px;
    background: rgba(102, 255, 193, 0.8);
  }

  .blueBox2 {
	width: 660px;
	height: 400px;
-webkit-clip-path: polygon(0 13%, 100% 0, 83% 100%, 0 82%);
clip-path: polygon(0 13%, 100% 0, 83% 100%, 0 82%);
	background: #66FFC1;
	background-size: cover;
	margin-left: 200px;
	margin-top: -90px;
	z-index: 10;
}

.naostro {
	position: absolute;
	font-size: 70px;
	text-align: left;
	line-height: 80px;
	color: #0D5C63;
	margin-top: 80px;
	margin-left: -120px;
	font-weight: bold;
}

.zkusenosti {
	width: 500px;
	position: absolute;
	font-size: 24px;
	z-index: 5;
	text-align: left;
	color: #0D5C63;
	margin-left: 200px;
