.container {
	display: flex;
	padding: 0 6%;
	justify-content: flex-end;
	align-items: center;
}
.yellowBox {
	width: 642px;
	height: 500px;
	-webkit-clip-path: polygon(2% 20%, 93% 0, 100% 100%, 0 61%);
	clip-path: polygon(2% 20%, 93% 0, 100% 100%, 0 61%);
	background: url('./asset2.jpg') center center no-repeat;
	background-size: cover;
	margin-top: -250px;
	z-index: 10;
}

  .yellowBox:before {
    content: '';
  	position: absolute;
  	top: 0;
  	right: 0;
  	bottom: 0;
  	left: 0;
  	background: rgba(241,255,0,0.8);
  }

.zbyva {
	padding: 0 5%;
	font-size: 40px;
	margin-top: 80px; 
	margin-right: -50px; 
	text-align: left;
	line-height: 65px;
	color: #0D5C63;
	font-weight: bold;
	position: absolute;
	z-index: 11;
}

.cas {
	font-size: 80px;
	font-weight: bold;
	display: inline-block;
}

.day, .hour, .min, .sec {
	display: block;
}

.sec {
	position: absolute;
	z-index: 5;
}

.zbyva1 {
	font-size: 40px;
