.space {
	height: 700px;
	margin-top: 500px;
}

.blueBackground{
	background: #0D5C63;
	height: 100%;
	-webkit-clip-path: polygon(0 0, 100% 30%, 100% 70%, 0% 100%);
	clip-path: polygon(0 0, 100% 30%, 100% 70%, 0% 100%);
	z-index: 10;
	display: flex;
	justify-content: center;
	align-items: center;
}

.redObject {
	width: 900px;
	height: 500px;
	background: url('./asset3.jpg') center center no-repeat;
	background-size: cover;
	-webkit-clip-path: polygon(0 0, 100% 25%, 88% 93%, 39% 100%);
	clip-path: polygon(0 0, 100% 25%, 88% 93%, 39% 100%);
	position: relative;
	z-index: 5;
	margin-bottom: -900px;
}

.redObject:before {
    content: '';
  	position: absolute;
  	top: 0;
  	right: 0;
  	bottom: 0;
  	left: -200px;
    background: rgba(255, 57, 72, 0.7);
  }

  .prvni {
  	font-size: 70px;
  	color: white;
  	line-height: 80px;
  	margin-left: -200px;
  }