

.redBackground{
	background: rgba(255, 57, 72, 1);
	height: 700px;
	display: flex;
	-webkit-clip-path: polygon(0 30%, 100% 0, 100% 100%, 0 70%);
clip-path: polygon(0 30%, 100% 0, 100% 100%, 0 70%);
	justify-content: center;
	align-items: center;
	padding: 0 10%;
}

.yellow2 {
	width: 850px;
	height: 500px;
	background: url('./asset4.jpg') center center no-repeat;
	background-size: cover;
	-webkit-clip-path: polygon(0 0, 100% 30%, 100% 70%, 0% 100%);
	clip-path: polygon(0 0, 100% 30%, 100% 70%, 0% 100%);
	position: relative;
	margin-top: -300px;
	margin-left: 200px;
	z-index: 5;
}

.yellow2:before {
    content: '';
  	position: absolute;
  	top: 0;
  	right: 0;
  	bottom: 0;
  	left: 0;
    background: rgba(255, 255, 0, 0.7);
  }

 .druhy {
  	font-size: 70px;
